.Carousel{
    height:530px;
    width:100%;
    overflow:hidden;
    margin:0 auto;
}
.cimg{
    height:530px;
    width:100%;
}
@media screen and (max-width:768px){
    .Carousel{
        height:380px;
    }
    .cimg{
        height:380px;
    }
} 
@media screen and (max-width:480px){
    .Carousel{
        height:270px;
    }
    .cimg{
        height:270px;
    }
} 